
.nav-header {
    font-family: 'Quicksand', sans-serif;
    background: #5515B5;
    color: #FFDC00;
    height: 100px;
    clear: both;
    margin: 0;
    width: 100%;
    font-size: 2.5vw;
    z-index: 99;
}
  
  .nav-header li {
  
    display: inline-block;
    width: 33.3%;
    text-align: center;
  }
  
  
  .nav-header h1 {
    
    font-size: 50px;
  }


