body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.container {
  display: flex;
  flex-wrap: wrap;
  max-width: 980px;
  justify-content: center;
  margin: auto;
}


.nav-header {
    font-family: 'Quicksand', sans-serif;
    background: #5515B5;
    color: #FFDC00;
    height: 100px;
    clear: both;
    margin: 0;
    width: 100%;
    font-size: 2.5vw;
    z-index: 99;
}
  
  .nav-header li {
  
    display: inline-block;
    width: 33.3%;
    text-align: center;
  }
  
  
  .nav-header h1 {
    
    font-size: 50px;
  }


  @media screen and (max-width: 400px) {
 
      .nav-header {
        font-family: 'Quicksand', sans-serif;
        background: #5515B5;
        color: #FFDC00;
        height: 100px;
        clear: both;
        margin: 0;
        width: 100%;
        font-size: 2.5vw;
        z-index: 99;
    }
      
      .nav-header li {
      
        display: inline-block;
        width: 33.3%;
        text-align: center;
      }
      
      
      .nav-header h1 {
        
        font-size: 15px;
      }
  }
  
  @media screen and (max-width: 1100px) {
      .nav-header {
        font-family: 'Quicksand', sans-serif;
        background: #5515B5;
        color: #FFDC00;
        height: 100px;
        clear: both;
        margin: 0;
        width: 100%;
        font-size: 2.5vw;
        z-index: 99;
    }
      
      .nav-header li {
      
        display: inline-block;
        width: 33.3%;
        text-align: center;
      }
      
      
      .nav-header h1 {
        
        font-size: 30px;
      }
  }


.card {
    background: #7FFFAC;
    border-radius: 2px;
    height: 175px;
    margin: 1rem;
    position: relative;
    float: right;
    width: 175px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    display: block;
  }
  
  .card > .img-container {
  
    height: 100%;
    overflow: hidden;
  }
  .card > .img-container > img {
    
    width: 100%;
  }
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

