

.card {
    background: #7FFFAC;
    border-radius: 2px;
    height: 175px;
    margin: 1rem;
    position: relative;
    float: right;
    width: 175px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    display: block;
  }
  
  .card > .img-container {
  
    height: 100%;
    overflow: hidden;
  }
  .card > .img-container > img {
    
    width: 100%;
  }